/* Global styles for all screen sizes */
.global-margin {
  margin-left: 20px;
  margin-right: 20px;
}

.full-width {
  width: 100%;
  margin: 0;
  padding: 0;
}

.bg-wavy-gradient-blue {
  background: radial-gradient(circle at 20% 30%, rgba(25, 227, 242, 0.15), rgba(255, 255, 255, 0) 20%),
              radial-gradient(circle at 70% 50%, rgba(0, 180, 254, 0.1), rgba(255, 255, 255, 0) 15%),
              radial-gradient(circle at 40% 70%, rgba(0, 18, 63, 0.1), rgba(255, 255, 255, 0) 10%);
  background-color: white;
  background-size: 100% 100%;
}


.image-overlay-light-blue {
  position: absolute;
  inset: 0; /* Cover the entire image */
  background: linear-gradient(
    to right,
    #19E3F2, /* Lighter blue */
    #00B4FE, /* Darker in the middle */
    #19E3F2 /* Lighter blue */
  );
  z-index: 0; /* Make sure it's above the image */
} 

.image-overlay-dark-blue {
  position: absolute;
  inset: 0; /* Cover the entire image */
  background: linear-gradient(
    to right,
    #19E3F2, /* Lighter blue */
    #00123F, /* Darker in the middle */
    #19E3F2 /* Lighter blue */
  );
  z-index: 0; /* Make sure it's above the image */
}


@media (min-width: 1500px) {
  .global-margin {
    margin-left: 200px;
    margin-right: 200px;
  }
}
